import { css } from "emotion";
import { Color } from "reactism/utils";
// assets
import pengaduan from "reactism/static/icon/pengaduan.svg";
import tilang from "reactism/static/icon/tilang.svg";
import lelang from "reactism/static/icon/lelang.svg";

const ItemFeature = () => {
  const wrapper = css`
    display: grid;
    grid-template-columns: repeat(3, 25%);
    justify-content: center;
    grid-column-gap: 4em;
    grid-row-gap: 1em;
    h1 {
      margin-top: 5rem;
    }
    > div {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 12% auto 1fr;
      height: 48vh;
      span.icon {
        justify-self: end;
        img {
          width: 120px;
        }
      }
      border-radius: 8px;
      padding: 15px 15px 30px 15px;
    }
    div:nth-child(1) {
      background-color: ${Color.success};
      color: #fff;
    }
    div:nth-child(2) {
      background-color: ${Color.warning};
      color: #fff;
    }
    div:nth-child(3) {
      background-color: ${Color.darkWarning};
      color: #fff;
    }

    /* MOBILE */
    @media (max-width: 700px) {
      grid-template-columns: 1fr;
      grid-column-gap: 0;
      > div {
        height: 44vh;
      }
    }
  `;
  return (
    <div className={wrapper}>
      <div>
        <span className="icon">
          <img src={pengaduan} alt="icon pengaduan" />
        </span>
        <h1>Pengaduan</h1>
        <p>
          Lorem ipsum, dolor sit amet consectetur adipisicing elit. Inventore,
          aliquam quibusdam. Nostrum repellat culpa ut id. Qui soluta dolor unde
          sunt repellat repudiandae? Sequi tempora quam saepe. Cum, animi
          consequuntur?
        </p>
      </div>
      <div>
        <span className="icon">
          <img src={tilang} alt="icon pengaduan" />
        </span>
        <h1>Tilang</h1>
        <p>
          Lorem ipsum, dolor sit amet consectetur adipisicing elit. Inventore,
          aliquam quibusdam. Nostrum repellat culpa ut id. Qui soluta dolor unde
          sunt repellat repudiandae? Sequi tempora quam saepe. Cum, animi
          consequuntur?
        </p>
      </div>
      <div>
        <span className="icon">
          <img src={lelang} alt="icon pengaduan" />
        </span>
        <h1>Lelang</h1>
        <p>
          Lorem ipsum, dolor sit amet consectetur adipisicing elit. Inventore,
          aliquam quibusdam. Nostrum repellat culpa ut id. Qui soluta dolor unde
          sunt repellat repudiandae? Sequi tempora quam saepe. Cum, animi
          consequuntur?
        </p>
      </div>
    </div>
  );
};

export default ItemFeature;
